<template>
  <div class="account-coupon">
    <div class="account-head">
      <div class="head-title">我的帳戶</div>
    </div>
    <div class="account-nav">
      <div class="nav-text-box">
        <div @click="goToClass" class="nav-text">我的課程</div>
        <div @click="goToCoupon" class="nav-text">折價券</div>
      </div>
      <div class="nav-line"></div>
    </div>
    <div v-if="currentNav === 'class'" class="account-class">
      <!-- <div class="class-emtpy-text">你還沒有購買任何課程喔！</div> -->
    </div>
    <div v-else class="account-coup">
      <!-- <div class="coup-emtpy-text">購買我們的課程，或是參與 Reborn 臉書粉絲團活動就有機會獲得折價券。</div> -->
    </div>
  </div>
</template>

<script>

export default {
  name: 'AccountCoupon',
  components: {

  },
  data () {
    return {
      currentNav: 'class'
    }
  },
  methods: {
    goToClass () {
      this.currentNav = 'class'
    },
    goToCoupon () {
      this.currentNav = 'coupon'
    }
  }
}
</script>
<style lang="scss" scoped>
.account-coupon {
    max-width: 1000px;
    margin: auto;
    padding: 40px 48px 96px;

    .account-head {
        max-width: 800px;
        display: flex;
        align-items: flex-end;

        .head-title {
            font-size: 38px;
            margin-bottom: 10px;
            font-weight: bold;
        }
    }

    .account-nav {
      margin-top: 64px;
    }

    .nav {

      &-text-box {
        display: flex;
      }

      &-text {
        font-size: 16px;
        margin-right: 40px;
        padding-bottom: 11px;
        cursor: pointer;

        &:hover {
          font-weight: bold;
          border-bottom: 1px solid #25282B;
        }
      }

      &-line {
        width: 100%;
        height: 1px;
        background-color: #E0E0E0;
      }
    }

    .account-class {
      margin-top: 50px;

      .class {

        &-emtpy-text {
          font-size: 40px;
          color: #A0A4A8;
        }
      }
    }

    .account-coup {
      margin-top: 50px;

      .coup {

        &-emtpy-text {
          font-size: 40px;
          color: #A0A4A8
        }
      }
    }
}

</style>



